*,
*::before,
*::after {
  box-sizing: inherit; }

html {
  box-sizing: border-box; }

img {
  display: block;
  max-width: 100%;
  height: auto; }

.visually-hidden {
  border: 0;
  clip: rect(1px 1px 1px 1px);
  height: 1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px; }

.banner {
  min-height: 796px; }

.button {
  background-color: transparent;
  border: 1px solid #ffffff;
  border-radius: 5px;
  color: #ffffff; }

.container {
  margin: 0 auto;
  max-width: 1440px;
  min-width: 320px;
  padding: 0 15px; }
  @media (max-width: 1023px) {
    .container {
      padding: 0 25px; } }
  @media (max-width: 1023px) {
    .container {
      padding: 0 17px; } }

.logo {
  display: flex;
  align-items: center; }

.logo__img {
  margin-right: 16px; }
  @media (max-width: 767px) {
    .logo__img {
      height: 38px;
      width: 38px; } }

@media (max-width: 767px) {
  .logo__text {
    display: none; } }

.logo__brand {
  color: #ffffff;
  font-family: "Roboto Condensed", sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 25px;
  margin: 0;
  line-height: 29px;
  padding: 0; }

.logo__tagline {
  color: #788891;
  font-family: "Roboto", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  margin: 0;
  padding: 0; }

.page-header {
  align-items: center;
  background-color: transparent;
  display: flex;
  justify-content: space-between;
  padding: 33px 0; }
  @media (max-width: 1023px) {
    .page-header {
      padding: 20px 0; } }
  @media (max-width: 767px) {
    .page-header {
      padding: 17px 0; } }

.page-header__contacts {
  align-items: center;
  display: flex; }

.page-header__contacts-link {
  align-items: center;
  color: #ffffff;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  font-family: "PT Mono", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  margin-left: 88px;
  position: relative;
  text-decoration: none; }
  .page-header__contacts-link:hover, .page-header__contacts-link:focus {
    opacity: 0.8; }
  @media (max-width: 1023px) {
    .page-header__contacts-link {
      margin-left: 32px; } }
  @media (max-width: 767px) {
    .page-header__contacts-link {
      margin-left: 23px; } }
  .page-header__contacts-link::before {
    content: "";
    background-repeat: no-repeat;
    background-position: 50% 50%;
    margin-right: 20px;
    flex-shrink: 0; }
    @media (max-width: 1023px) {
      .page-header__contacts-link::before {
        margin-right: 0; } }
  .page-header__contacts-link--email::before {
    background-image: url("../img/envelope.svg");
    background-size: 19px 14px;
    height: 14px;
    width: 19px; }
    @media (max-width: 1023px) {
      .page-header__contacts-link--email::before {
        background-size: 24px 19px;
        height: 19px;
        width: 24px; } }
  .page-header__contacts-link--phone::before {
    background-image: url("../img/phone-call.svg");
    background-size: 18px 18px;
    height: 18px;
    width: 18px; }
    @media (max-width: 1023px) {
      .page-header__contacts-link--phone::before {
        background-size: 24px 24px;
        height: 24px;
        width: 24px; } }

@media (max-width: 1023px) {
  .page-header__contacts-text {
    display: none; } }

.page-header__button {
  color: #ffffff;
  font-family: "Roboto Condensed", sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 14px;
  text-align: center;
  letter-spacing: 0.05em;
  margin-left: 46px;
  padding: 14px 27px;
  text-transform: uppercase; }
  .page-header__button:hover, .page-header__button:focus {
    background-color: #00b2ff;
    border-color: #00b2ff; }
  @media (max-width: 1023px) {
    .page-header__button {
      display: none; } }

.wrapper {
  background-image: url("../img/banner.png");
  background-color: #1b2d37;
  min-width: 320px; }
